import { Injectable } from '@angular/core';
import { MenuItem } from '../../class/menu-item';

@Injectable()
export class DingtalkService {
  private corpid: String = '';

  constructor() { }

  setCorpid(corpid: String) {
    this.corpid = corpid;
  }

  /**
   * 设置导航栏标题
   * @param title 标题
   */
  setTitle(title: String) {
    dd.ready(function(){
      dd.biz.navigation.setTitle({
        title : title,
        onSuccess : function(result) {
        },
        onFail : function(err) {}
      });
    });
  }

  /**
   * 分享当前页面
   */
  share() {
    dd.ready(function() {
      dd.biz.util.share({
        style: 0,
        url: 'http://' + window.location.host,
        onSuccess : function(res) {},
        onFail : function(err) {}
      });
    });
  }

  showPreloader(text: string = '',
    showIcon: boolean = true,
    onSuccess?: Function,
    onFail?: Function) {
    dd.ready(function() {
      dd.device.notification.showPreloader({
        text: text,
        showIcon: true,
        onSuccess : function(result) {
          onSuccess(result);
        },
        onFail : function(err) {
          onFail(err);
        }
      });
    });
  }

  hidePreloader(onSuccess?: Function, onFail?: Function) {
    dd.ready(function() {
      dd.device.notification.hidePreloader({
        onSuccess : function(result) {
          onSuccess(result);
        },
        onFail : function(err) {
          onFail(err);
        }
      });
    });
  }

  /**
   * toast
   * @param text 提示信息
   * @param icon icon样式，有success和error，默认为空 0.0.2
   * @param duration 显示持续时间，单位秒，默认按系统规范[android只有两种(<=2s >2s)]
   * @param delay 延迟显示，单位秒，默认0
   */
  toast(text: String,
    icon: String = '',
    onSuccess?: Function,
    onFail?: Function,
    duration: Number = 1,
    delay: Number = 0) {
    dd.ready(function(){
      dd.device.notification.toast({
        icon: 'success',
        text: text,
        duration: 1,
        delay: 0,
        onSuccess : function(result) {
          onSuccess(result);
        },
        onFail : function(err) {
          onFail(err);
        }
      });
    });
  }

  /**
   * confirm
   */
  setNavigationConfirm(message: string,
    onSuccess?: Function,
    onFail?: Function,
    title: string = '提示',
    buttonLabels: String[] = ['取消', '确认']) {
      dd.ready(function(){
        dd.device.notification.confirm({
          message: message,
          title: title,
          buttonLabels: buttonLabels,
          onSuccess : function(result) {
            onSuccess(result);
          },
          onFail : function(err) {
            onFail(err);
          }
        });
      });
  }

  /**
   * 返回上级页面
   */
  goBack(onSuccess?: Function, onFail?: Function) {
    dd.ready(function(){
      dd.biz.navigation.goBack({
        onSuccess : function(result) {
          onSuccess(result);
        },
        onFail : function(err) {
          onFail(err);
        }
      });
    });
  }

  /**
   * 设置左侧导航按钮
   * @param onSuccess 按钮点击事件回调
   */
  setNavigationLeft(onSuccess?: Function, onFail?: Function) {
    dd.ready(function(){
      // 设置左侧导航按钮
      if (dd.ios) {
        dd.biz.navigation.setLeft({
          control: true,
          text: '',
          onSuccess : function(result) {
            onSuccess(result);
          },
          onFail : function(err) {
            onFail(err);
          }
        });
      }else if (dd.android) {
        document.addEventListener('backbutton', function(e) {
          onSuccess(e);
          e.preventDefault();
        });
      }
    });
  }

  setNavigationMenu(items: MenuItem[],
    onSuccess: Function,
    onFail?: Function,
    backgroundColor?: String,
    textColor?: String) {
    dd.ready(function(){
      dd.biz.navigation.setMenu({
          items : items,
          onSuccess: function(data) {
            onSuccess(data);
          },
          onFail: function(err) {
            onFail(err);
          }
      });
    });
  }

  /**
   * 设置导航栏右侧单个按钮
   * @param text 控制显示文本，空字符串表示显示默认文本
   * @param show 控制按钮显示， true 显示， false 隐藏， 默认false
   * @param control 是否控制点击事件，true 控制，false 不控制， 默认false
   * @param onSuccess 如果control为true，则onSuccess将在发生按钮点击事件被回调
   * @param onFail 错误事件被回调
   */
  setNavigationRight(text: string = '',
    show: boolean = false,
    control: boolean = false,
    onSuccess?: Function,
    onFail?: Function) {
    dd.ready(function(){
      dd.biz.navigation.setRight({
        show: show,
        control: control,
        text: text,
        onSuccess : function(result) {
          onSuccess(result);
        },
        onFail : function(err) {
          onFail(err);
        }
      });
    });
  }
}



