import { Component, OnInit, OnDestroy } from '@angular/core';
import { SettingService } from '../../../../services/setting/setting.service';
import { SvgIconRegistryService } from 'angular-svg-icon';
import { WorkMenu, WorkMenuItem } from '../../../../class/work-menu';

@Component({
  selector: 'app-page-work',
  templateUrl: './page-work.component.html',
  styleUrls: ['./page-work.component.css']
})
export class PageWorkComponent implements OnInit, OnDestroy {
  list: WorkMenu[];

  constructor(private settings: SettingService, private iconReg: SvgIconRegistryService) {
    this.list = settings.getWorkMenuList();
    for (let i = 0; i < this.list.length; i++) {
      const svgPath = 'assets/icon/' + this.list[i].icon  + '.svg';
      this.iconReg.loadSvg(svgPath);
    }
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    for (let i = 0; i < this.list.length; i++) {
      const svgPath = 'assets/icon/' + this.list[i].icon  + '.svg';
      this.iconReg.loadSvg(svgPath);
    }
  }

  onClick(i, j) {
    const title = this.list[i].title + '-' + this.list[i].list[j].title;
    this.settings.setTitle(title);
  }

  onAddClick(index) {
    let title = '新增';
    switch (index) {
      case 0:
        title += '现场签证申报';
        break;
      case 1:
        title += '设计变更申报';
        break;
      case 2:
        title += '产值申报';
        break;
      case 3:
        title += '合同台账';
        break;
      default:
        break;
    }
    this.settings.setTitle(title);
  }

}



