import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { PageWorkComponent } from './page-work.component';
import { PageAddComponent } from './pages/page-add/page-add.component';
import { PageWorkListComponent } from './pages/page-work-list/page-work-list.component';
import { PageInventoryComponent } from './pages/page-add/pages/page-inventory/page-inventory.component';
import { PageAddWorkComponent } from './pages/page-add/pages/page-add-work/page-add-work.component';

const workRoutes: Routes = [
  {
    path: 'work',
    component: PageWorkComponent,
    children: [
      {
        path: 'add',
        component: PageAddComponent,
        children: [
          { path: 'inventory', component: PageInventoryComponent },
          { path: 'add/:res/:type', component: PageAddWorkComponent },
          { path: ':res/:type', redirectTo: '/work/add/add/:res/:type' }
        ]
      },
      { path: 'work/:res/:type', component: PageWorkListComponent },
      { path: ':res/:type', redirectTo: '/work/work/:res/:type' }
    ]
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(workRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class WorkRoutingModule { }



