import { Injectable } from '@angular/core';
import { MenuTab } from '../../class/menu-tab';
import { SmsSegmented } from '../../class/sms-segmented';
import { SmsNotice } from '../../class/sms-notice';
import { WorkMenu, WorkMenuItem } from '../../class/work-menu';

import { DingtalkService } from '../../services/dingtalk/dingtalk.service';
import { LoggerService } from '../../services/logger/logger.service';
import { SmsUpcomingService } from '../../services/sms-upcoming/sms-upcoming.service';
import { SmsNoticeService } from '../../services/sms-notice/sms-notice.service';

import { Subject } from 'rxjs/Subject';

@Injectable()
export class SettingService {
  private tabList: MenuTab[];
  private userMenuList: MenuTab[];
  private settingMenuList: MenuTab[];
  private smsSegmentedList: SmsSegmented[];
  private navTitle: String;
  private titleList: String[] = [];
  private workMenuList: WorkMenu[];

  private titleSource = new Subject<String>();
  title$ = this.titleSource.asObservable();

  constructor(private dingtalk: DingtalkService,
    private upcoming: SmsUpcomingService,
    private notice: SmsNoticeService,
    private logger: LoggerService) {
    this.setTitle('易协同1.0');

    this.tabList = [
      new MenuTab('消息', 'sms', '/home/sms'),
      new MenuTab('工作台', 'work', '/home/work'),
      new MenuTab('我', 'user', '/home/user')
    ];

    this.userMenuList = [
      new MenuTab('个人信息', 'info', '/user/info'),
      new MenuTab('邀请好友', 'share', '', '#FF8C00'),
      new MenuTab('设置', 'setting', '/user/setting', '#1E90FF'),
      new MenuTab('关于', 'about', '/user/about', '#00BFFF')
    ];

    this.settingMenuList = [new MenuTab('清除缓存')];

    this.smsSegmentedList = [
      new SmsSegmented('待办', '/home/sms/upcoming'),
      new SmsSegmented('通知', '/home/sms/notice'),
    ];

    this.workMenuList = [
      new WorkMenu('现场签证', 'onsite', '#d4237a', [
        new WorkMenuItem('签证申报', '/work/declare/onsite', '/work/add/declare/onsite'),
        new WorkMenuItem('完工确认', '/work/confirm/onsite', '/work/add/confirm/onsite'),
      ]),
      new WorkMenu('设计变更', 'onsite', '#1296db', [
        new WorkMenuItem('变更申报', '/work/declare/design', '/work/add/declare/design'),
        new WorkMenuItem('完工确认', '/work/confirm/design', '/work/add/confirm/design'),
      ]),
      new WorkMenu('付款管理', 'onsite', '#259B24', [
        new WorkMenuItem('产值申报', '/work/declare/payment', '/work/add/declare/payment'),
        new WorkMenuItem('付款申请', '/work/confirm/payment', '/work/add/confirm/payment'),
      ]),
      new WorkMenu('合同管理', 'onsite', '#ff9800', [
        new WorkMenuItem('合同台账', '/work/declare/contract', '/work/add/declare/contract'),
        new WorkMenuItem('合同结算', '/work/confirm/contract', '/work/add/confirm/contract'),
      ])
    ];
  }

  /**
   * 获取页面标题
   */
  getTitle(): String {
    return this.navTitle;
  }

  /**
   * 判断标题是否是默认标题
   */
  isTitle(): Boolean {
    return this.navTitle === this.titleList[0];
  }

  /**
   * 设置页面标题
   * @param title 标题,为空是即恢复默认标题
   */
  setTitle( title: String = '') {
    if (title === '') {
      const index = this.titleList.length - 1;
      if (index > 0) {
        title = this.titleList.pop();
      }
    }else {
      this.titleList.push(title);
    }
    // this.logger.log(this.titleList);
    title = this.titleList[this.titleList.length - 1];
    this.navTitle = title;
    if (dd.version == null) {
      this.titleSource.next(title);
    } else {
      this.dingtalk.setTitle(title);
    }
  }

  /**
   * 获取底部导航栏菜单列表
   */
  getTabList(): MenuTab[] {
    return this.tabList;
  }

  /**
   * 获取工作台菜单列表
   */
  getWorkMenuList(): WorkMenu[] {
    return this.workMenuList;
  }

  /**
   * 获取‘我’个人菜单列表
   */
  getUserMenuList(): MenuTab[] {
    return this.userMenuList;
  }

  /**
   * 获取‘设置’个人菜单列表
   */
  getSettingMenuList(): MenuTab[] {
    return this.settingMenuList;
  }

  /**
   * 获取消息页面分段器列表
   * @param callback 回调函数，返回 SmsSegmented[] 类型
   */
  getsmsSegmentedList(callback?: Function): SmsSegmented[] {
    if (callback !== null) {
      this.upcoming.getList(list => {
        this.notice.getList(nlist => {
          this.smsSegmentedList[0].badge = ((list as SmsNotice[]).length);
          this.smsSegmentedList[1].badge = ((nlist as SmsNotice[]).length);
          callback(this.smsSegmentedList);
        });
      });
    }
    return this.smsSegmentedList;
  }

}



