import { Component, OnInit } from '@angular/core';
import { MenuTab } from '../../../../class/menu-tab';
import { SettingService } from '../../../../services/setting/setting.service';

@Component({
  selector: 'app-page-setting',
  templateUrl: './page-setting.component.html',
  styleUrls: ['./page-setting.component.css']
})
export class PageSettingComponent implements OnInit {
  list: MenuTab[];

  constructor(private settings: SettingService) {
    this.list = settings.getSettingMenuList();
  }

  ngOnInit() {
  }

  onClick(item: MenuTab) {
    switch (item.title) {
      case '清除缓存':
        this.clearCache();
        break;
      default:
        break;
    }
  }

  /**
   * 清除缓存
   */
  clearCache() {
    dd.ready(function(){
      dd.device.notification.showPreloader({
        text: '清除缓存中...',
        showIcon: true,
        onSuccess : function(res) {
          setTimeout(() => {
            dd.device.notification.hidePreloader({
              onSuccess : function(resu) {
                dd.device.notification.toast({
                  icon: 'success',
                  text: '清理完成',
                  duration: 1,
                  delay: 0,
                  onSuccess : function(r) {
                  },
                  onFail : function(err) {}
                });
              },
              onFail : function(err) {}
            });
          }, 2000);
        },
        onFail : function(err) {}
      });
    });
  }

}



