export class User {

    constructor(id: Number,
        public nickName: String,
        public  isAvatar: Boolean,
        public  avatar: String,
        public  phoneNumber: String,
        public  character: String = '甲方现场人员',
        public  job: String = '监理工程师') {
    }

    nameToString(): String {
        return '您好，' + this.nickName;
    }
}



