import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { SvgIconRegistryService } from 'angular-svg-icon';
import { SettingService } from '../../../../../../services/setting/setting.service';
import { LoggerService } from '../../../../../../services/logger/logger.service';
import { DingtalkService } from '../../../../../../services/dingtalk/dingtalk.service';

@Component({
  selector: 'app-page-add-work',
  templateUrl: './page-add-work.component.html',
  styleUrls: ['./page-add-work.component.css']
})
export class PageAddWorkComponent implements OnInit, OnDestroy {
  isInventory = false;

  constructor(private settings: SettingService,
    private dingtalk: DingtalkService,
    private logger: LoggerService,
    private iconReg: SvgIconRegistryService,
    private route: ActivatedRoute) {
      const that = this;
      this.dingtalk.setNavigationRight('提交', true, true, result => {
        that.dingtalk.showPreloader('提交中...', true, res => {
          setTimeout(() => {
            that.dingtalk.hidePreloader(results => {
              that.dingtalk.toast('提交成功', 'success');
            });
          }, 1000);
        });
      });
      this.iconReg.loadSvg('assets/icon/avatar/avatar.svg');
      this.iconReg.loadSvg('assets/icon/avatar/avatar2.svg');
      this.route.paramMap.subscribe((params: ParamMap) => {
        this.initUi(params.get('type'));
      });
    }

  ngOnInit() {
  }

  initUi(type: String) {
  }

  ngOnDestroy() {
    this.iconReg.unloadSvg('assets/icon/avatar/avatar.svg');
    this.iconReg.unloadSvg('assets/icon/avatar/avatar2.svg');
    if ( this.isInventory ) {
      this.settings.setTitle('工作量清单');
    }else {
      this.settings.setTitle();
    }
    this.dingtalk.setNavigationRight();
  }

}



