import { Component, OnInit, OnDestroy } from '@angular/core';
import { User } from '../../../../class/user';
import { MenuTab } from '../../../../class/menu-tab';
import { SvgIconRegistryService } from 'angular-svg-icon';
import { UserService } from '../../../../services/user/user.service';
import { SettingService } from '../../../../services/setting/setting.service';
import { DingtalkService } from '../../../../services/dingtalk/dingtalk.service';

@Component({
  selector: 'app-page-user',
  templateUrl: './page-user.component.html',
  styleUrls: ['./page-user.component.css']
})
export class PageUserComponent implements OnInit, OnDestroy {
  user: User;
  userName: String;
  menus: MenuTab[];

  constructor(private dingtalk: DingtalkService,
    private settings: SettingService,
    private users: UserService,
    private iconReg: SvgIconRegistryService) {
    users.getUser((user) => {
      this.user = user;
      this.userName = user.nameToString();
      if (!user.isAvatar) {
        const svgPath = 'assets/icon/avatar/' + user.avatar  + '.svg';
        this.iconReg.loadSvg(svgPath);
      }
    });
    this.menus = settings.getUserMenuList();
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    if (!this.user.isAvatar) {
      const svgPath = 'assets/icon/avatar/' + this.user.avatar  + '.svg';
      this.iconReg.unloadSvg(svgPath);
    }
  }

  onClick(item: MenuTab) {
    this.settings.setTitle(item.title);
    if (item.icon === 'share') {
      this.dingtalk.share();
    }
  }

}



